import { Component, OnInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import { BackendService } from '../backend.service';
import { MatDialog } from '@angular/material';
import { ViewEditBoxComponent } from '../view-edit-box/view-edit-box.component';
import { Router } from '@angular/router';

export interface UserData {
  id: string;
  username: string;
  fathername:string,
  mobile:string,
  adhar:string,
  reference: string;
  userId : string
}


@Component({
  selector: 'app-user-table',
  templateUrl: './user-table.component.html',
  styleUrls: ['./user-table.component.css']
})
export class UserTableComponent implements OnInit {
  users = []
  displayedColumns: string[] = ['id', 'username', 'fatherName','mobile','adhar','reference','edit'];
  dataSource: MatTableDataSource<UserData>;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  constructor( 
    private backend:BackendService,
    private dialog:MatDialog,
    private router : Router
    
    ) {

      this.loadData();
   // console.log(users)
    // Assign the data to the data source for the table to render
   
  }

  ngOnInit() {


    if(this.backend.checkSession()){

      let dt = this.backend.getUserFromLocalStorage();

      dt.userType == 'user' ?  this.router.navigate(['/view-dash']) : this.loadData();
      
    
    }else{
      this.router.navigate(['/home']);
    }
    
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }


  viewData(uid){

    let db = this.dialog.open(ViewEditBoxComponent,{
      data:uid
    });

    db.afterClosed().subscribe(()=>{
      this.loadData();
    })
  } //View Data ends here


  loadData(){

    this.users = [];
    this.backend.getProfile()
    .subscribe((response)=>{
      let i = 1
      response.data.forEach(dt => {
        console.log(dt);
        if(dt.username !== "admin"){
          this.users.push({
            id : i,
            username : dt.username,
            fathername:dt.fatherName,
            mobile:dt.mobile,
            adhar:dt.additionalData.adharCard,
            reference : dt.reference || 0,
            entryside : dt.entrySide,
            userId    : dt.userId
          })
          i++;
        }
       
      });
      this.dataSource = new MatTableDataSource(this.users);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  } //Load data ends here



} //Main class ends here
