import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { BackendService } from '../backend.service';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { CookieService } from 'ngx-cookie-service';
import { MatDialog } from '@angular/material';
import { DialogBoxComponent } from '../dialog-box/dialog-box.component';

@Component({
  selector: 'app-admin-dash',
  templateUrl: './admin-dash.component.html',
  styleUrls: ['./admin-dash.component.css']
})
export class AdminDashComponent implements OnInit {
  orgaArr = [];
  empArr  = [];
  numberPattern = "^(0|[1-9][0-9]*)$";
  minDate:Date;
  maxDate:Date;

  constructor(
    private backend:BackendService,
    private router:Router,
    private toster:ToastrService,
    private cookies:CookieService,
    private dialog: MatDialog
    ) {


      const currentYear = new Date().getFullYear();
      this.minDate = new Date(currentYear - 80, 0, 1);
      this.maxDate = new Date(currentYear - 18,0,1);

     }

  ngOnInit() {
    //this.backend.getEmployee();
    if(this.backend.checkSession()){

      let dt = this.backend.getUserFromLocalStorage();

      dt.userType == 'user' ?  this.router.navigate(['/view-dash']) : this.loadTreeData();
      
    
    }else{
      this.router.navigate(['/home']);
    }
   

  }

  onProfileSubmit(empdt:NgForm) {

    let data = {
                    
      username        : empdt.value.username,
      userType        : "user",
      firstName       : empdt.value.username,
      fatherName      : empdt.value.fathername,
      email           : empdt.value.email,
      mobile          : empdt.value.phone,
      db              : new Date(empdt.value.birthDate),
      adhar           : empdt.value.adhar,
      account         : empdt.value.account,
      reference       : empdt.value.reference,
      entrySide       : empdt.value.entry        

    }
    this.backend.newProfileCreation(data)
    .subscribe((res)=>{
      if(res.status == 200){

      this.orgaArr = this.empArr.filter(dt => dt.empid == res.data.reference)
        //console.log(new Date(empdt.value.birthDate));
      const empLen = this.empArr.length;
      const lastEmpId = this.empArr[empLen - 1].empid;
      const obj = {
        empid      : lastEmpId + 1,
        empname    : res.data.username,
        parentid   : this.orgaArr[0].parentid + 1,
        empdesgid  : "",
      // tslint:disable-next-line:radix
      supervisorid : parseInt(res.data.reference),
      empdesgname  : ""
    };
    this.empArr.push(obj); 
    //this.empArr = this.backend.getEmployee("new");
    empdt.resetForm();
    this.dialog.open(DialogBoxComponent);
    console.log('this.empArr => ', this.empArr);
    //this.empdetails.emit(this.empArr);
      }else{
       this.toster.error("Phone number already exists");
      }
    })


    //Without Backend
     
    // this.orgaArr = this.empArr.filter(dt => dt.empid == empdt.value.reference)
    //     //console.log(new Date(empdt.value.birthDate));
    //   const empLen = this.empArr.length;
    //   const lastEmpId = this.empArr[empLen - 1].empid;
    //   const obj = {
    //   empid: lastEmpId + 1,
    //   empname: empdt.value.username,
    //   parentid: this.orgaArr[0].parentid + 1,
    //   empdesgid: "",
    //   // tslint:disable-next-line:radix
    //   supervisorid: parseInt(empdt.value.reference),
    //   empdesgname: ""
    // };
    // this.backend.pushDataEmpArray(obj); 
    // this.empArr = this.backend.getEmployee();
    // empdt.resetForm();
    // this.dialog.open(DialogBoxComponent);
    // console.log('this.empArr => ', this.empArr);
    // //this.empdetails.emit(this.empArr);



    
  }

  checkPhoneNumber(event){
    if(isNaN(event.data)){
      console.log("Invalid Phone");
    }else{
      console.log("Valid Phone");
    }
  }


  loadTreeData(){
    this.backend.getEmployee("get")
          .subscribe((res)=>{
            res['data'].forEach(data => {
              //console.log("Server Data" , data)
               // this.empArra.push();
                this.orgaArr = this.empArr.filter(dt => dt.empid == data.reference)
               console.log("Orga Structre Data", this.orgaArr);
              const empLen = this.empArr.length;
              const lastEmpId = empLen == 0 ? 0 : this.empArr[empLen - 1].empid;
              //const desgId    = this.employees[empLen - 1].empdesgid;
              const obj = {
              "empid": lastEmpId + 1,
              "empname": data.username,
              "parentid": this.orgaArr.length>0 ? Number(this.orgaArr[0].parentid + 1) : 0,
              "empdesgid": 2,
              // tslint:disable-next-line:radix
              "supervisorid": data.reference ? parseInt(data.reference):0,
              "empdesgname": ""
            };
  
              console.log("Tree Object ", obj)
              this.empArr.push(obj); 
              
            }); 
            
          })       
  }



  logout(){
    this.cookies.delete('auth');
    this.router.navigate(['/home']);
  }

} //Main Class ends here
