import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';


import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { RouterModule } from '@angular/router';
import { FormsModule} from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { AdminDashComponent } from './admin-dash/admin-dash.component';
import { ViewDashComponent } from './view-dash/view-dash.component';
import { OrgaChartDirective } from './orga-chart.directive';
import { CookieService } from 'ngx-cookie-service';
import { ToastrModule } from 'ngx-toastr';
import { MaterialModule } from './material/material.module';
import { DialogBoxComponent } from './dialog-box/dialog-box.component';
import { UserTableComponent } from './user-table/user-table.component';
import { ViewEditBoxComponent } from './view-edit-box/view-edit-box.component';


@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    AdminDashComponent,
    ViewDashComponent,
    OrgaChartDirective,
    DialogBoxComponent,
    UserTableComponent,
    ViewEditBoxComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    FormsModule,
    HttpClientModule,
    MaterialModule,
    ToastrModule.forRoot({
      timeOut: 2000,
      positionClass: 'toast-top-center',
      preventDuplicates: true
    }),
    RouterModule.forRoot([
      {path:"",redirectTo:'/home',pathMatch:"full"},
      {path:"home",component:HomeComponent},
      {path:"admin-dash",component:AdminDashComponent},
      {path:"view-dash",component:ViewDashComponent},
      {path:"profile-dash",component:UserTableComponent},
      {path:"**",redirectTo:'/home',pathMatch:"full"},
    ])
  ],
  providers: [CookieService],
  entryComponents:[DialogBoxComponent,ViewEditBoxComponent],
  bootstrap: [AppComponent]
})
export class AppModule { }
