import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { BackendService } from '../backend.service';
import { Router,Route } from '@angular/router'
import { CookieService } from 'ngx-cookie-service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],

})
export class HomeComponent implements OnInit {

  constructor( 
    private backend:BackendService,
    private router:Router,
    private cookies:CookieService,
    private toaster:ToastrService
    ) { }

  ngOnInit() {

    if(this.backend.checkSession()){
        //let loggedData = this.backend
    }else{

    }

  } //ngOnint ends here

  loginSubmit(form:NgForm){

    let redirectComp;
    let data = {
      username : form.value.username,
      password : form.value.password
    }
    this.backend.signIn(data)
    .subscribe((resp)=>{
      if(resp.status === 200){
          this.cookies.set('auth',resp.data.authToken);
          this.backend.addUserToLocalStorage(resp.data.userDetails);
          redirectComp = resp.data.userDetails.userType == "admin" ? '/admin-dash' : 'view-dash';
          console.log(redirectComp);
          setTimeout(() => {
            this.router.navigate([redirectComp]);
          }, 1000);
          this.toaster.success("Login Successfull")
        
      }else{
        this.toaster.error(resp.message)
      }
    })
  }

}//Main Class ends here
