import { Component, OnInit, Inject ,AfterViewInit} from '@angular/core';
import { NgForm, Form } from '@angular/forms';
import { MAT_DIALOG_DATA,MatDialog } from '@angular/material';
import { BackendService } from '../backend.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-view-edit-box',
  templateUrl: './view-edit-box.component.html',
  styleUrls: ['./view-edit-box.component.css']
})
export class ViewEditBoxComponent implements AfterViewInit {
  empArr;
  referenceArray;
  loader = false;
  numberPattern = "^(0|[1-9][0-9]*)$";
  minDate:Date;
  maxDate:Date;

  //Form Variables
  user;
  father;
  ref;
  entr;
  adh;
  acc;
  mob;
  dob;
  mail;


  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    private backend: BackendService,
    private dialog : MatDialog,
    private router:Router
    ) {

     this.backend.getProfile()
     .subscribe(dt=>{
       this.empArr = dt.data;
     })

     this.referenceArray = this.backend.getTreeData()
   
   }

   ngAfterViewInit() {
     this.loadData();
  } //NgViewInit Ends here


  updateProfile(empdt:NgForm){

    this.loader = true;
    let data = {
      userId          : this.data,              
      username        : empdt.value.username,
      firstName       : empdt.value.username,
      fatherName      : empdt.value.fathername,
      email           : empdt.value.email,
      mobile          : empdt.value.phone,
      db              : new Date(empdt.value.birthDate),
      adhar           : empdt.value.adhar,
      account         : empdt.value.account,
      reference       : empdt.value.reference,
      entrySide       : empdt.value.entry        

    }
     this.backend.updateProfile(data)
     .subscribe((resp)=>{
       console.log(resp);
       setTimeout(() => {
        this.loader = false;
        this.dialog.closeAll();
       }, 1000);
       
     })
  }


  loadData(){
  this.loader = true;
  setTimeout(() => {
   // console.log(this.empArr);
    let filter = this.empArr.filter(dt=> dt.userId == this.data && dt.userType != 'admin');
    
  this.user   = filter[0].username;
  this.father = filter[0].fatherName;
  this.ref    = filter[0].reference;
  this.adh    = filter[0].additionalData.adharCard;
  this.entr   = filter[0].entrySide;
  this.acc    = filter[0].additionalData.accountNo;
  this.dob    = filter[0].additionalData.dob;
  this.mail   = filter[0].email;
  this.mob    = filter[0].mobile;
  
  console.log("Filtered data",filter);
  
  this.referenceArray = this.referenceArray.map((dt)=>{
    return {empid : dt.empid, empname : dt.empname};
  })
  console.log(this.referenceArray)
  this.loader = false;
  }, 1000)
}
}//Main class ends here
